<h1 id="welcome" tabindex="-1"><a class="header-anchor" href="#welcome" aria-hidden="true">#</a> Welcome!</h1>
<p>Welcome to the course <em>Client-Server Communication</em>! In this course you'll learn how to create software systems consisting of two parts: a client application and a server application. It will be very similar to how traditional web applications works, but in this course we will focus on implementing an API on the server any type of client can use to communicate with it (such as a smartphone application), and not only web browsers. And to practice on using APIs you'll also learn how to create Single-Page Applications using a frontend framework.</p>
<div class="custom-container warning"><p class="custom-container-title">Note to old students</p>
<p>If you're an old student that took this course a previous year but still have some examination tests left to pass you take those tests as they are described in this year's instructions.</p>
